.card{
  background-color: rgba(0,0,0,0.7);
  border-radius: 30px;
  padding:2rem;
  height:100%;
  width:100%;
  text-align: center;
}
.questionData{
  color: #f8f8f8;
  font-size: 3rem;
}

input {
  background-color: rgba(4, 4, 17, 0.666);
  color: #f8f8f8;
  letter-spacing: 1px;
  padding: 0.5rem;
  margin: 0;
  text-align: center;
  width:100px;
  height: 50px;
  font-size: 2rem;
}

.btn {
  box-shadow: var(--box-shadow);
  color: #f8f8f8;
  border-radius: 30px;
  border: 0;
  display: block;
  font-size: 25px;
  margin: 10px 0 20px;
  padding: 10px;
  width: 100%;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
}

.play-btn-container .play-btn{
background-color:#57b846;
transition: 0.3s linear all;
width:100%
}

.play-btn:hover{
  background-color: darkgreen;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12),0 1px 5px 0 rgba(0,0,0,0.2);
}
